import React from "react";
import { Table } from "reactstrap";
import { round } from "mathjs";
import Card from "./Card";

const WeatherList = (props) => {
  return (
    <div className="container">
      <div className="row justify-content-center">
        <div className="col-sm">
          <h1
            style={{ fontSize: "60px", textAlign: "center", margin: "100px" }}
          >
            {props.city.name}
          </h1>
        </div>
      </div>
      <div className="row">
        {props.weather.map((w, i) => {
          let cl = 2;

          if (i === 0) {
            cl = "4 offset-4";

            return (
              <div className="row">
                <Card singleWeather={w} column={cl} />
              </div>
            );
          } else {
            cl = 2;
            return <Card singleWeather={w} column={cl} />;
          }
        })}

        {/*  <Table>
          <thead>
            <tr>
              <th></th>
              <th>Date</th>
              <th>Temperature</th>
              <th>Sky</th>
            </tr>
          </thead>
          <tbody>
            {props.weather.map((w) => (
              <tr key={w.dt}>
                <td>
                  <img
                    src={`http://openweathermap.org/img/wn/${w.weather[0].icon}@2x.png`}
                  />
                </td>
                <td>{w.dt_txt}</td>
                <td>{round(w.main.temp)}</td>
                <td>{w.weather[0].main}</td>
              </tr>
            ))}
          </tbody>
        </Table> */}
      </div>
    </div>
  );
};

export default WeatherList;
