import React from "react";
import { round } from "mathjs";

const Card = (props) => {
  return (
    <div className={`col-sm-${props.column}`} key={props.singleWeather.dt}>
      <h1 className="card-title" style={{ textAlign: "center" }}>
        {round(props.singleWeather.main.temp)}&#176;
      </h1>
      <h2 className="card-title" style={{ textAlign: "center" }}>
        {props.singleWeather.weather[0].main}
      </h2>
      <div className="card mb- shadow-sm" style={{ marginBottom: "15%" }}>
        <img
          style={{
            marginTop: "-20%",
            marginBottom: "-20%",
            textAlign: "center",
            border: "10px",
          }}
          src={`https://openweathermap.org/img/wn/${props.singleWeather.weather[0].icon}@4x.png`}
          className="card-img-top"
          alt="samplepic"
          align="middle"
        />
        <div className="card-body">
          <h5 className="card-title" style={{ textAlign: "center" }}>
            {props.singleWeather.dt_txt}
          </h5>
        </div>
      </div>
    </div>
  );
};

export default Card;
