import React from "react";
import SearchBar from "./SearchBar";
import WeatherList from "./WeatherList";
import axios from "axios";

export default class App extends React.Component {
  state = { city: [], weather: [], getCity: "" };

  getCity = (event) => {
    this.setState({
      getCity: event.target.value,
    });
  };

  async componentDidUpdate(prevProps, prevState) {
    if (this.state.getCity !== prevState.getCity) {
      const baseURL = `https://api.openweathermap.org/data/2.5/forecast?q=${this.state.getCity.replace(
        " ",
        "+"
      )}&units=metric&appid=e367751780c81b487741413cc1056391`;
      const response = await axios.get(baseURL);
      const city = response.data.city;
      const weather = response.data.list.slice(0, 15);
      this.setState({
        city: city,
        weather: weather,
      });
    }
  }

  render() {
    return (
      <div className="container">
        <div className="row">
          <div className="col-xs-12">
            <SearchBar getCity={this.getCity} />
          </div>
        </div>
        <div className="row">
          <div className="col-xs-12">
            <WeatherList city={this.state.city} weather={this.state.weather} />
          </div>
        </div>
      </div>
    );
  }
}
